import { Injectable } from '@angular/core'
import { HttpClient } from '@angular/common/http'

// format of entries in HMBD database
export interface Table {
  primary_key: string
  metal: string
  gene: string
  id: string
  description: string
  accession: string
  gene_max_length: string
  pubmed_link: string
  ncbi_gene_link: string
  location: string
}

@Injectable({
  providedIn: 'root',
})
export class HmbdService {
  constructor(public http: HttpClient) {}

  /* retrieve the tables of HMBD database from api's and store each on a Table interface */
  getSalmonella() {
    return this.http.get<Table[]>('/api/salmonella')
  }

  getEscherichia() {
    return this.http.get<Table[]>('/api/escherichia')
  }

  getKlebsiella() {
    return this.http.get<Table[]>('/api/klebsiella')
  }
}
