import { Component, OnInit } from '@angular/core'
import { HmbdService, Table } from '../hmbd.service'
import { InputTextModule } from 'primeng/inputtext'
import { SortEvent } from 'primeng/api/sortevent'
import { FilterUtils } from 'primeng/utils'

interface SearchColumn {
  name: string
  code: string
}

@Component({
  selector: 'app-hmbd',
  templateUrl: './hmbd.component.html',
  styleUrls: ['./hmbd.component.css'],
})
export class HmbdComponent implements OnInit {
  // tables of HMBD database
  table: Table[]
  salmonella: Table[]
  klebsiella: Table[]
  escherichia: Table[]

  // search filter variables
  searchTerm: InputTextModule
  cols: any[]

  constructor(public hmbdService: HmbdService) {
    // on startup: load database from server and show salmonella
    this.setup()
    this.getSalmonella()
    this.getEscherichia()
    this.getKlebsiella()
  }

  ngOnInit() {
    // define table columns, sort icons
    this.cols = [
      { field: 1, sortIcon: false, header: 'Metal', class: 'row-1' },
      { field: 2, sortIcon: true, header: 'Gene', class: 'row-2' },
      { field: 3, sortIcon: false, header: 'ID', class: 'row-3' },
      {
        field: 4,
        sortIcon: true,
        header: 'Description',
        class: 'row-4',
      },
      { field: 5, sortIcon: false, header: 'Accession', class: 'row-5' },
      {
        field: 6,
        sortIcon: false,
        header: 'Gene Max Length',
        class: 'row-6',
      },
      { field: 7, sortIcon: false, header: 'Pubmed', class: 'row-7' },
      { field: 8, sortIcon: false, header: 'NCBI Gene', class: 'row-8' },
      {
        field: 9,
        sortIcon: false,
        header: 'Location',
        class: 'row-9',
      },
    ]

    FilterUtils['custom'] = (value, filter): boolean => {
      if (filter === undefined || filter === null || filter.trim() === '') {
        return true
      }

      if (value === undefined || value === null) {
        return false
      }

      return parseInt(filter) > value
    }
  }

  // on startup: load salmonella from server and display on front-end
  setup() {
    this.hmbdService.getSalmonella().subscribe(
      (data) => {
        this.table = data
      },
      (error) => {
        alert('Could not retrieve Salmonella table.')
      }
    )
  }

  // pre-load salmonella from server
  getSalmonella() {
    this.hmbdService.getSalmonella().subscribe(
      (data) => {
        this.salmonella = data
      },
      (error) => {
        alert('Could not retrieve Salmonella table.')
      }
    )
  }

  // pre-load escherichia from server
  getEscherichia() {
    this.hmbdService.getEscherichia().subscribe(
      (data) => {
        this.escherichia = data
      },
      (error) => {
        alert('Could not retrieve Escherichia table.')
      }
    )
  }

  // pre-load klebsiella from server
  getKlebsiella() {
    this.hmbdService.getKlebsiella().subscribe(
      (data) => {
        this.klebsiella = data
      },
      (error) => {
        alert('Could not retrieve Klebsiella table.')
      }
    )
  }

  // display salmonella
  showSalmonella() {
    this.table = this.salmonella
  }

  // display escherichia
  showEscherichia() {
    this.table = this.escherichia
  }

  // display klebsiella
  showKlebsiella() {
    this.table = this.klebsiella
  }

  // algorithm to sort table by columns
  sortBy(event: SortEvent) {
    event.data.sort((data1, data2) => {
      const value1 = data1[event.field]
      const value2 = data2[event.field]
      let result = null

      if (value1 == null && value2 != null) result = -1
      else if (value1 != null && value2 == null) result = 1
      else if (value1 == null && value2 == null) result = 0
      else if (typeof value1 === 'string' && typeof value2 === 'string')
        result = value1.localeCompare(value2)
      else result = value1 < value2 ? -1 : value1 > value2 ? 1 : 0

      return event.order * result
    })
  }
}
